<template>
  <div class="login_form_f ">
    <div>
      <div class="logo_header">
        <img src="../assets/logoheader.png" alt="">
      </div>
      <div class="login_form">
        <div class="text-danger text-center">{{errors}}</div>
        <form @submit.prevent="signUp()">
          <div class="form_input">
<!--            <label class="form_label">Login</label>-->
            <div class="input_f">
              <input  type="text" placeholder="username" v-model="username">
            </div>
          </div>
          <div class="form_input">
<!--            <label class="form_label">-->
<!--              <div class="password_show">-->
<!--                <div>Parol</div>-->
<!--                <div @click="show_password()" class="show_password">-->
<!--                  <span>-->
<!--                   <b-icon icon="eye-fill" aria-hidden="true" v-if="password"></b-icon>-->
<!--                   <b-icon icon="eye-slash-fill" aria-hidden="true" v-else></b-icon>-->
<!--                  </span>-->
<!--                </div>-->
<!--              </div>-->
<!--            </label>-->
            <div class="input_f password_label">
              <input  type="password" placeholder="Parol" v-model="password_name" id="password">
              <div @click="show_password()" class="show_password">
                <span>
                 <b-icon icon="eye-fill" aria-hidden="true" v-if="password"></b-icon>
                 <b-icon icon="eye-slash-fill" aria-hidden="true" v-else></b-icon>
                </span>
              </div>
            </div>
          </div>
          <div class="form_input text-center">
            <button>Kirish</button>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return{
      password:false,
      password_name:'',
      username:'',
      errors:''
    }
  },
  methods:{
    show_password(){
      const passwordField = document.querySelector('#password')
      if (passwordField.getAttribute('type') === 'password') passwordField.setAttribute('type', 'text')
      else passwordField.setAttribute('type', 'password')
      this.password=!this.password
    },
    signUp(){
          this.$axiosDefault.post('https://backend-tashabbus.minsport.uz/api/token/',{
            "username":this.username,
            "password":this.password_name
          })
          .then(res=>{
            localStorage.setItem('access',res.data.access)
            localStorage.setItem('refresh',res.data.refresh)
            localStorage.setItem('avatar',res.data.avatar)
            localStorage.setItem('email',res.data.email)
            localStorage.setItem('full_name',res.data.full_name)
            localStorage.setItem('username',res.data.username)
            this.$router.push('/')
          })
        .catch(()=>{
          this.errors="username yoki parol xato"
        })
    }
  }

}
</script>
<style>
body{
  background: #F9F9FB;
}
.login_form_f{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.login_form {
  width: 440px;
  height: 344px;
  background: #FFFFFF;
  border: 0;
  border-radius: 12px;
  padding: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  }
  .form_input{
    width: 100%;
    margin-bottom: 44px;
  }
  .form_label{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #666666;
    width: 100%;
  }
  .input_f{
    border: 1px solid rgba(102, 102, 102, 0.35);
    border-radius: 5px;
    width: 240px;
    height: 40px;
    margin-top: 10px;
    padding-left: 10px;
  }
  .input_f input{
    padding: 0;
    width: 100%;
    height: 100%;
    outline: none;
    border: 0;
    font-size: 15px;
    background: none;

  }
  .form_input button{
    width: 110px;
    height: 33px;
    background: #7020FF;
    border-radius: 5px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: #FFFFFF;
    border: 0;
  }
  .password_show{
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 240px;
  }
  .show_password{
    background: #C1C7CD;
    width: 40px;
    height: 38px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    border-radius: 5px;
    margin-right: -1px;
  }
  .logo_header{
    display: flex;
    justify-content: center;
    margin-bottom: 90px;
  }
  .password_label{
    display: flex;
  }
</style>
