<template>
  <div class="home">
    <Sidebar></Sidebar>
    <div>
      <div class="page-container">
        <div class="header">
            <div>
              <h4 class="">Sportchi kiritish</h4>
            </div>
          <div>
<!--            <button class="btn btn-primary" >Mahhal qo'shish +</button>-->
            <b-modal id="modal-1 v-b-modal.modal-1" title=" Agarda sizning mahhalangiz nomi chiqmasa shu yerdan qo'shing">
              <div>
                  <div>
                    <model-list-select
                        :list="options1"
                        v-model="objectItem"
                        option-value="code"
                        option-text="name"
                        placeholder="Viloyatni tanlang"
                        @input="getregion()"
                    >
                    </model-list-select>
                  </div>
                  <div class="my-4">
                    <model-list-select
                        :list="options2"
                        v-model="objectItem1"
                        option-value="code"
                        option-text="name"
                        placeholder="Tumanni tanlang"
                        @input="getkvartals()"
                    >
                    </model-list-select>
                  </div>

                  <div class="my-4">
                    <input type="text" class="form-control" placeholder="Mahhalla nomi" v-model="district_name" aria-describedby="basic-addon1">
                  </div>
                <div>
                  <button class="btn btn-primary" @click="setDistrict()">Qo'shish</button>
                </div>
              </div>
            </b-modal>
          </div>
        </div>
        <form class="main_form" @submit.prevent="createSportsmen()">
        <div class="row">
          <div class="col-xl-8">
            <div class="card_main">
                <div>
                  <model-list-select
                      :list="options1"
                      v-model="objectItem"
                      option-value="code"
                      option-text="name"
                      placeholder="Hudud tanlang"
                      @input="getregion()"
                  >
                  </model-list-select>
                </div>
                <div class="my-4">
                  <model-list-select
                      :list="options2"
                      v-model="objectItem1"
                      option-value="code"
                      option-text="name"
                      placeholder="Tumanni tanlang"
                      @input="getkvartals()"
                  >
                  </model-list-select>
                </div>
                <div class="my-4">
                  <model-list-select
                      :list="options3"
                      v-model="objectItem2"
                      option-value="code"
                      option-text="name"
                      placeholder="Mahallani tanlang"
                  >
                  </model-list-select>
                </div>
                <div class="my-4">
                  <model-list-select
                      :list="options4"
                      v-model="objectItem3"
                      option-value="code"
                      option-text="name"
                      placeholder="Sektorni tanlang"
                  >
                  </model-list-select>
                </div>
                <div class="my-4">
                  <model-list-select
                      :list="options5"
                      v-model="objectItem4"
                      option-value="code"
                      option-text="name"
                      placeholder="Sport turi tanlang"
                  >
                  </model-list-select>
                </div>
                <div class="my-4">
                  <input type="text" class="form-control" placeholder="FIO" v-model="name">
                </div>
                  <div class="quill_editors">
                    <div class="my-3">
                      Tavsiya
                    </div>
                    <quill-editor
                        :required="true"
                        ref="myQuillEditorOrg"
                        v-model="content"
                    />
                  </div>

            </div>
          </div>
          <div class="col-xl-4">
            <div class="card_main">
              <div class="my-3">
                Tug'ilgan sanasi
              </div>
              <VueDatePicker v-model="date"
             :min-date="minDate"
             :max-date="maxDate"
              />

              <div class="my-4">
                <model-list-select
                    :list="options6"
                    v-model="objectItem5"
                    option-value="code"
                    option-text="name"
                    placeholder="Yosh oralig'i"
                >
                </model-list-select>
              </div>
<!--              <div class="avater_setimage">-->
<!--                <div class="my-3">-->
<!--                  Avatar img-->
<!--                </div>-->
<!--                  <label class="avatar_label" for="avatar">-->
<!--                    <b-icon icon="camera" font-scale="5"></b-icon>-->
<!--                  </label>-->
<!--                <input type="file" id="avatar" class="d-none">-->
<!--              </div>-->
              <div class="my-4">
                <div class="my-3">
                  Jins
                </div>
                <div class="form-check">
                  <input class="form-check-input" type="radio" name="exampleRadios" v-model="gender" id="exampleRadios1" value="O'g'il" checked>
                  <label class="form-check-label" for="exampleRadios1">
                    Erkak
                  </label>
                </div>
                <div class="form-check">
                  <input class="form-check-input" type="radio" name="exampleRadios" v-model="gender" value="Qiz">
                  <label class="form-check-label" for="exampleRadios2">
                    Ayol
                  </label>
                </div>
              </div>
              <div class="my-4">
                <div class="my-3">
                  Natijani tanlang
                </div>
                <b-form-group >
                  <b-form-checkbox-group
                      id="checkbox-group-1"
                      v-model="selected"
                      :options="options"
                      name="flavour-1"
                  ></b-form-checkbox-group>
                </b-form-group>
              </div>
              <button class="btn btn-primary">Saqlash</button>
            </div>
          </div>

        </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import Destricts from '@/Json/districts.json'
import Regions from '@/Json/regions.json'
import Kvartals from '@/Json/kvartals.json'
import { VueDatePicker } from '@mathieustan/vue-datepicker';
import '@mathieustan/vue-datepicker/dist/vue-datepicker.min.css';
import {
  quillEditor
} from "vue-quill-editor";
import Sidebar from "../../components/Sidebar";
import { ModelListSelect } from 'vue-search-select'
export default {
  name: 'Home',
  components: {
    Sidebar,
    ModelListSelect,
    VueDatePicker,
    quillEditor

  },
  data () {
    return {
      selected: [], // Must be an array reference!
      options: [
      ],
      gender:'Erkak',
      name:'',
      district_name:'',
      content:'',
      date:new Date(),
      options1: [
      ],
      options2: [
      ],
      options3: [
      ],
      options4: [
        {
          code:1,
          name:"Hokim sektori"
        },
        {
          code:2,
          name:"Prokuror sektori"
        },
        {
          code:3,
          name:"Ichki Ishlar sektori"
        },
        {
          code:4,
          name:"Davlat Soliq sektori"
        }
      ],

      options5: [
        {
          code:1,
          name:"Workout"
        },
        {
          code:2,
          name:"Stritbol"
        },
        {
          code:3,
          name:"Voleybol"
        },
        {
          code:4,
          name:"Shashka"
        },
        {
          code:5,
          name:"Stol tennis"
        },
        {
          code:6,
          name:"Yengil atletika"
        },
      ],

      options6: [
        {
          code:1,
          name:"7-10"
        },
        {
          code:2,
          name:"11-15"
        },
          {
          code:3,
          name:"16-21"
        },
        {
          code:4,
          name:"22-26"
        },
        {
          code:5,
          name:"27-30"
        },
      ],
      objectItem: {},
      objectItem1: {},
      objectItem2: {},
      objectItem3: {},
      objectItem4: {},
      objectItem5: {},
      stringItem: '',
      regions:[],
      minDate: '1950',
      maxDate: '2040',
      results:[]
    }
  },
  mounted() {
    this.$axiosDefault.get('https://backend-tashabbus.minsport.uz/Json/kvartals.json')
    Regions.forEach(el=>{
      this.options1.push({
        code:el.id,
        name:el.name
      })
    })

    this.$axiosDefault.get('results/')
        .then(res=>{
          console.log(res.data)
          res.data.forEach(el=>{
            this.options.push(
              {
                text:el.name,
                value: {
                  "id":el.id,
                  "name":el.name,
                }
              })
          })
        })
  },
  methods:{
    createSportsmen(){
      var form={
        "city": this.objectItem.code,
        "region": this.objectItem1.code,
        "district": this.objectItem2.code,
        "birthdate": this.date,
        "sector": this.objectItem3.name,
        "name": this.name,
        "age": this.objectItem5.name,
        "avatar": null,
        "created_by": "",
        "recommendation": this.content,
        "gender": this.gender,
        "sport_type": this.objectItem4.name,
        "proof": null,
        "results": this.selected
      }
      this.$axiosDefault.post('admin-sportsman/',form,
          {
            headers: {
              'Authorization': 'Bearer ' + localStorage.getItem('access'),
              'Accept-Language':'ru'
            }
          }
      )
      .then(()=>{
        this.$toasted.show("Muvaffaqiyat kiritildi",{
          duration: 1000,
          'type':'info'
        })


        this.selected= [],
            this.options= [
            ],
            this.gender='Erkak',
            this.name='',
            this.district_name='',
            this.content='',
            this.date=new Date(),
            this.options1= [
            ],
            this.options2= [
            ],
            this.options3= [
            ],
            this.options4= [
              {
                code:1,
                name:"Hokim sektori"
              },
              {
                code:2,
                name:"Prokuror sektori"
              },
              {
                code:3,
                name:"Ichki Ishlar sektori"
              },
              {
                code:4,
                name:"Davlat Soliq sektori"
              }
            ],

            this.options5= [
              {
                code:1,
                name:"Workout"
              },
              {
                code:2,
                name:"Stritbol"
              },
              {
                code:3,
                name:"Voleybol"
              },
              {
                code:4,
                name:"Shashka"
              },
              {
                code:5,
                name:"Stol tennis"
              },
              {
                code:6,
                name:"Yengil atletika"
              },
            ],

            this.options6= [
              {
                code:1,
                name:"7-10"
              },
              {
                code:2,
                name:"11-15"
              },
              {
                code:3,
                name:"16-21"
              },
              {
                code:4,
                name:"22-26"
              },
              {
                code:5,
                name:"27-30"
              },
            ],
            this.objectItem= {},
            this.objectItem1= {},
            this.objectItem2= {},
            this.objectItem3= {},
            this.objectItem4= {},
            this.objectItem5= {},
            this.stringItem= '',
            this.regions=[],
            this.minDate= '1950',
            this.maxDate= '2040',
            this.results=[]
        this.$axiosDefault.get('https://backend-tashabbus.minsport.uz/Json/kvartals.json')
        Regions.forEach(el=>{
          this.options1.push({
            code:el.id,
            name:el.name
          })
        })

        this.$axiosDefault.get('results/')
            .then(res=>{
              console.log(res.data)
              res.data.forEach(el=>{
                this.options.push(
                    {
                      text:el.name,
                      value: {
                        "id":el.id,
                        "name":el.name,
                      }
                    })
              })
            })

      })
      .catch(error=>{
        if (error.response.status ===401) {
          localStorage.removeItem('access')
          localStorage.removeItem('access')
          localStorage.removeItem('refresh')
          localStorage.removeItem('avatar')
          localStorage.removeItem('email')
          localStorage.removeItem('full_name')
          localStorage.removeItem('username')
          this.$router.go('/login')
        }
        this.$toasted.show("Kiritishda xatolik bor",{
          duration: 1000,
          'type':'error'

        })
      })
    },
    getregion(){
      this.options2=[]
      this.options3=[]

      Destricts.filter(tree => tree.parent === this.objectItem.code).forEach(el=>{
        this.options2.push({
          code:el.id,
          name:el.name
        })
      })
    },
    getkvartals(){

      this.options3=[]
      Kvartals.filter(tree => tree.parent === this.objectItem1.code).forEach(el=>{
        this.options3.push({
          code:el.id,
          name:el.name
        })
      })
    },
    // setDistrict(){
    //   let client= {
    //         "id":Kvartals[Kvartals.length-1].id+1,
    //         "name": this.district_name,
    //         "parent": this.objectItem1.code,
    //         "parent_name":this.objectItem1.name
    //       }
    //     this.$axiosDefault.post('http://localhost:8080/Json/kvartals.json',client)
    // }
  }
}
</script>
<style>
.custom-control-label span{
  margin-left: 10px;
}
</style>
